/* src/App.css */

body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #c3c3c3;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
}

main {
  padding: 20px;
}

section {
  margin-bottom: 30px;
}

.clasa{
  color: rgb(255, 255, 255);
}

h2{
  color: #282c34;
}

p {
  color: #555;
  line-height: 1.5;
}

.image-container {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
}

footer {
  background-color: #282c34;
  padding: 10px;
  color: white;
  position: fixed;
  bottom: 0;
  width: 100%;
}

section {
  margin-bottom: 30px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media screen and (min-width: 768px) {
  h2{
    margin-left: 70px;
    margin-right: 70px;
  }
}

@media screen and (max-width: 768px) {
  section {
    margin-top: 50px;
    flex-direction: column;
  }
}

h1{
  margin-top: 50px;
}

h2 {
  color: #444;
  line-height: 1.5;
}

h3 {
  color: #555;
  line-height: 1.5;
}

/* Styles for alternating image positioning */
section:nth-child(even) .image-container {
  order: 1;
}

section:nth-child(odd) .image-container {
  order: 2;
}

.image-container {
  flex: 1;
}

img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


img,
h2,
h3 {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  animation: fadeIn 1s ease-in-out;
}